
export const createHabit = ({title, date}) => ({
    type: 'CREATE_HABIT',
    title,
    date
})

export const removeHabit = (title) => ({
    type: 'REMOVE_HABIT',
    title
})

export const resetHabit = (title) => ({
    type: 'RESET_HABIT',
    title
})


